.polls li {
  background-color: var(--light);
  color: var(--blue);
  padding: 1.5rem;
  text-align: center;
  width: 800px;
  margin: auto;
  height: 6px;

  
}

.polls li:nth-child(odd) {
  background-color: white;
  
}

.polls li:hover {
  border: 2px solid var(--blue);
}

.poll-title {
  color: var(--blue);
  font-size: 2rem;
  margin-top: 1rem;
  text-align: center;
  text-transform: uppercase;
}



