.navbar {
  background-color: var(--blue);
  padding: 22px 0;
  margin-bottom: 30px;
}

.navbar-brand {
  background-color: var(--light);
  color: var(--blue);
  text-decoration: none;
  padding: 0.5rem;
  border-radius: 0.5rem;
  text-transform: uppercase;
  font-weight: bold;
}

.navbar-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.navbar-item {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.navbar-user {
  margin-top: 0.5rem;
  text-align: right;
  color: var(--light);
}



